import React, { Component } from 'react';
import Carousel from 'react-bootstrap/lib/Carousel';
import photo1 from './photos/bh1.jpg';
import photo2 from './photos/bh2.jpg';
import photo3 from './photos/bh3.jpg';
import photo4 from './photos/bh4.jpg';
import './Photos.css';

class Photos extends React.Component {

    constructor(props, context) {
      super(props, context);
  
      this.handleSelect = this.handleSelect.bind(this);
  
      this.state = {
        // index: 0,
        // direction: null
      };
    }
  
    handleSelect(selectedIndex, e) {
    //   alert(`selected=${selectedIndex}, direction=${e.direction}`);
      this.setState({
        index: selectedIndex,
        direction: e.direction
      });
    }
  
    render() {
      const { index, direction } = this.state;
  
      return (
        <Carousel
        //   activeIndex={index}
          direction={direction}
          onSelect={this.handleSelect}
          interval={6000}
        >
          <Carousel.Item>
            <img width={900} height={500} alt="900x500" src={photo1} />
            <Carousel.Caption>
              <h3></h3>
              <p></p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img width={900} height={500} alt="900x500" src={photo2} />
            <Carousel.Caption>
              <h3></h3>
              <p></p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img width={900} height={500} alt="900x500" src={photo3} />
            <Carousel.Caption>
              <h3></h3>
              <p></p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img width={900} height={500} alt="900x500" src={photo4} />
            <Carousel.Caption>
              <h3></h3>
              <p></p>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      );
    }
  }
  

export default Photos;
